import { useTranslation } from 'react-i18next'

const GroupClasses = () => {
  const [t] = useTranslation('global')

  return (
        <button className="uppercase bg-red-500 w-full text-white text-lg pt-3 pb-4 rounded-lg transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-110 hover:bg-orange-700 duration-300">
           {t('groupClasses.group')} <br /><span className="text-sm font-bold text-red-500 bg-zinc-50 px-2 py-1 rounded-xl">{t('groupClasses.submsg')}</span>
        </button>
  )
}

export default GroupClasses
